import React, { memo, useEffect, useRef } from "react";
import "amazon-connect-streams";
// const instanceName = "tscc-test-instance"

const Ccp = ({selectedInstance,onChildData }) => {
    const ref = useRef();
    useEffect(() => {
        try {
            // console.log("init start");
            if (typeof window === "undefined")
                throw new Error("window missing");
            if (typeof window.connect === "undefined")
                throw new Error("global connect missing");
            window.connect.core.initCCP(ref.current, {
                ccpUrl: `https://${selectedInstance}.my.connect.aws/connect/ccp-v2`,
                loginPopup: true,
                loginPopupAutoClose: true,
                loginOptions: {                 
                    autoClose: true,              
                    height: 600,                  
                    width: 400,                   
                    top: 250,                       
                    left: 800                       
                  },
                pageOptions: {
                    enableAudioDeviceSettings: true,
                    enablePhoneTypeSettings: true,
                },
                softphone: { allowFramedSoftphone: true },
                ccpAckTimeout: 5000, 
                ccpSynTimeout: 3000, 
                ccpLoadTimeout: 10000
            }
            );
            window.localStorage.removeItem('connectPopupManager::connect::loginPopup');
             function getContact(){
                window.connect.contact(function(contact){
                    contact.onAccepted(function(contact){
                        // console.log('onAccepted number format',JSON.stringify(contact));
                        const str=contact.getContactId();
                        const index = str.lastIndexOf("-");
                        const result = str.substring(index+1,str.length );
                        const customer_reference_number=result;
                        const phoneNumber = contact.getActiveInitialConnection().getEndpoint().phoneNumber;
                        const data = {
                            phoneNumber:phoneNumber,
                            customer_reference_number:customer_reference_number
                        };
                        onChildData(data);
                    })
                })
            }
            getContact();
            // console.log("init end");
        } catch (e) {
            // console.error(e);
        }
    }, [ref]);
    // console.log(ref)
    return (
        <div
            ref={ref}
            style={{
                width: "100%",
                height:"500px"
            }}
        />
    );
};

export default memo(Ccp);
